<template>
  <div>
    <Header :title="$route.params.name"></Header>
    <div id="area-details">
      <div class="profile-div">
        <div class="image-box">
          <img class="profile-image" :src="$route.params.img" :alt="$route.params.name"/>
        </div>
        <div class="box">
          <p>{{ description }}</p>
        </div>
      </div>
      <div class="section">
        <h2>Schools</h2>
        <SchoolData name="School Name"
                    gradeRange="Grades"
                    enrollment="Enrolled"
                    rating="Rating"
                    color="rgba(0,0,0,.1)">
        </SchoolData>
        <SchoolData v-for="school in schools"
                    :name="school.name"
                    :gradeRange="school.gradeRange"
                    :enrollment="school.enrollment"
                    :rating="school.rating">
        </SchoolData>
        <p>* Data provided by GreatSchools.org</p>
      </div>
      <div class="section">
        <h2>Nearby Fun</h2>
        <NearbyFun v-for="fun in nearbyFun"
                   :name="fun.name"
                   :description="fun.description">
        </NearbyFun>
      </div>
    </div>
  </div>
</template>

<script>
import api from '@/api/api'
import Header from '@/components/header/Header'
import SchoolData from '@/components/areas/SchoolData'
import NearbyFun from '@/components/areas/NearbyFun'

export default {
  props: ['name', 'img'],
  components: {
    Header,
    SchoolData,
    NearbyFun
  },
  data () {
    return {
      description: '',
      schools: [],
      nearbyFun: []
    }
  },
  created () {
    api.axios.get(`${api.baseURL}/areas/areaDetails/${this.$route.params.name}`)
      .then(res => {
        this.description = res.data.description
        this.schools = res.data.schools
        this.nearbyFun = res.data.nearbyFun
      })
      .catch(err => {
        console.log(err)
      })
  }
}
</script>

<style scoped>
  #area-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;
  }

  .profile-div {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-items: center;
    width: 90%;
    max-width: 900px;
  }

  .box {
    max-width: 400px;
  }

  .image-box {
    width: 300px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .profile-image {
    height: 300px;
  }

  .section {
    padding-top: 100px;
    width: 90%;
    max-width: 900px;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-items: center;
  }
</style>
