<template>
  <div>
    <Header :title="'Edit ' + $route.params.area"></Header>
    <AdminNav></AdminNav>

    <div id="edit-area-details">

      <div class="box">
        <h2>Description</h2>
        <textarea v-model="description"></textarea>
      </div>

      <div class="box">
        <h2>Nearby Fun</h2>
        <div class="nearby-fun-div" v-for="(fun, index) in nearbyFun">
          <input v-model="fun.name" placeholder="Name of Activity">
          <textarea v-model="fun.description" placeholder="Description of activity"></textarea>
          <button class="delete" @click="removeActivity(index)">Remove</button>
        </div>
      </div>

      <div class="box">
        <button class="add-another" @click="addNearbyFun">Add Another Activity</button>
      </div>

      <div class="buttons">
        <button class="delete" @click="cancelEdit">Cancel</button>
        <button @click="saveEdit">Save</button>
      </div>

    </div>
  </div>
</template>

<script>
import Header from '@/components/header/Header'
import AdminNav from '@/components/admin/AdminNav'
import api from '@/api/api'

export default {
  components: { Header, AdminNav },
  data () {
    return {
      description: '',
      nearbyFun: []
    }
  },
  created () {
    api.axios.get(`${api.baseURL}/areas/areaDetails/${this.$route.params.area}`)
      .then(res => {
        this.description = res.data.description
        this.nearbyFun = res.data.nearbyFun
        console.log(res)
      })
      .catch(err => {
        console.log(err)
      })
  },
  methods: {
    addNearbyFun () {
      this.nearbyFun.push({ name: '', description: '' })
    },
    removeActivity (index) {
      this.nearbyFun.splice(index, 1)
    },
    cancelEdit () {
      this.$router.push('/admin/edit/areas')
    },
    saveEdit () {
      api.axios.post(`${api.baseURL}/areas/updateArea/${this.$route.params.area}`, {
        description: this.description,
        nearbyFun: this.nearbyFun
      })
      .then(res => {
        console.log(res)
        this.$router.push('/admin/edit/areas')
      })
      .catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style scoped>
  #edit-area-details {
    padding-top: 50px;
  }

  .box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 50px;
  }

  .nearby-fun-div {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding-top: 25px;
  }

  textarea {
    width: 90%;
    max-width: 600px;
    height: 200px;
    margin-top: 25px;
    font-family: 'Raleway', helvetica, sans-serif;
  }

  input {
    width: 200px;
    font-family: 'Raleway', helvetica, sans-serif;
  }

  .buttons {
    display: flex;
    position: fixed;
    bottom: 0;
    background-color: rgba(0,0,0,.1);
    width: 100%;
  }

  button {
    background-color: #fff;
    width: 100px;
    padding: 5px;
    font-size: 18px;
    transition: 250ms all;
    color: var(--success);
    border: thin solid var(--success);
    border-radius: 10px;
    border-top: none;
    border-bottom: none;
    margin: 20px;
  }

  button:hover {
    color: #fff;
    background-color: var(--success);
    transform: scaleX(1.05);
    cursor: pointer;
  }

  button:focus {
    outline: none;
  }

  .delete {
    color: var(--coral);
    border-color: var(--coral);
  }

  .delete:hover {
    color: var(--leather);
    background-color: var(--coral);
  }

  .add-another {
    width: 200px;
  }
</style>
